<template>
    <div class="app-container">
        <transition name="fade" mode="out-in" appear>
            <Header />
        </transition>
        <transition name="fade" mode="out-in" appear>
            <Sidebar />
        </transition>
        <div class="app-main__outer">
            <div class="app-main__inner">
              <MfPreview v-bind:mfId="mfId" />
              <button class="btn btn-outline-primary p-3 pl-5 pr-5 mr-3" style="float:right;" type="button" v-on:click="editManufacturerDto()">Edit</button>				
            </div>
        </div>
        <transition name="fade" mode="out-in" appear>
            <Footer />
        </transition>
    </div>
</template>

<script>
    import Header from "../components/Header";
	import Sidebar from "../components/Sidebar";
    import MfPreview from "../components/Tables/MfPreview";
    import Footer from "../components/Footer";
    export default {
        name: 'app',
        components: {
            Header,
			Sidebar,
            MfPreview,
            Footer
        },
        props:['mfId'],
        methods: {
            editManufacturerDto() {
                this.$router.push({ name: 'MfEdit', params: { mfId: this.mfId }});
            },
        },
    }
</script>
